import React from 'react'
import Link from 'gatsby-link'

import Container from './Container'

const Footer = () => (
  <footer className='pv4 pv5-ns'>
    <Container className='flex flex-column'>
      <div className='flex-column mv2 mv0-ns'>
        <div className='flex flex-column flex-row-ns justify-between'>
          <p className='f7 mv0 mv2-ns'>
            © {(new Date()).getFullYear()} timshi.ai
          </p>
        </div>
        <p className='f7 mv0 mv2-ns'>
          <a className='link black-80 dim' href='https://github.com/rongierlach/yfr-dot-com' target='_blank'>Site</a>
          &nbsp;built with&nbsp;
          <a className='link black-80 dim' href='https://www.gatsbyjs.org/' target='_blank'>Gatsby</a>
          ,&nbsp;
          <a className='link black-80 dim' href='http://tachyons.io/' target='_blank'>Tachyons</a>
          ,&nbsp;
          <a className='link black-80 dim' href='https://reactjs.org/' target='_blank'>React</a>
          &nbsp;and&nbsp;
          <a className='link black-80 dim' href="https://www.contentful.com/" rel="nofollow" style={{top: "2px", position: "relative"}} target="_blank">
            <img src="https://images.contentful.com/fo9twyrwpveg/44baP9Gtm8qE2Umm8CQwQk/c43325463d1cb5db2ef97fca0788ea55/PoweredByContentful_LightBackground.svg" style={{height: "20px", width: "auto", filter: "grayscale(100%)"}} alt="Powered by Contentful" />
          </a>.
        </p>
      </div>
    </Container>
  </footer>
)

export default Footer



